.about__container {
  padding: 0 0 120px 250px;

  @media (min-width: 768px) {
    background-image: url("../img/about-year-2022.png");
    background-repeat: no-repeat;
    background-position: 0 0;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/about-year-2022@2x.png");
      background-size: 150px 320px;
    }
  }

  @media (max-width: 1919px) {
    padding: 0 50px 120px 250px;
  }

  @media (max-width: 1023px) {
    padding: 0 50px 80px 225px;
    background-position: 46px 0;
  }

  @media (max-width: 767px) {
    padding: 0 25px 77px;
  }
}

.about__title {
  margin: 0;
  margin-bottom: 13px;

  font-family: "Roboto Condensed", "Arial", "Helvetica", sans-serif;
  font-size: 36px;
  line-height: 50px;
  font-weight: 400;
  color: #041827;

  @media (max-width: 767px) {
    font-size: 28px;
    line-height: 39px;
  }
}

.about__text-wrapper {
  margin-bottom: 42px;

  line-height: 21px;

  @media (max-width: 1023px) {
    margin-bottom: 23px;
  }

  @media (max-width: 767px) {
    font-size: 14px;
    line-height: 19px;
    margin-bottom: 15px;
  }
}

.about__text {
  margin: 0;
  margin-bottom: 23px;

  @media (max-width: 767px) {
    margin-bottom: 18px;
  }
}

.about__button {
  width: 180px;
  padding: 13px 13px;
  border: 1px solid #00b2ff;
  border-radius: 3px;

  font-family: "Roboto Condensed", "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.07em;
  color: #00b2ff;

  background-color: inherit;

  cursor: pointer;
  user-select: none;

  @media (max-width: 767px) {
    width: 100%;
    padding: 8px 15px;

    font-size: 14px;
    line-height: 16px;
  }

  @media (width: 320px) {
    width: 160px;
  }

  &:hover {
    background-color: #6fa4cc;

    color: #ffffff;
  }
}

.is-close .about__text--hidden {
  display: none;
}

.is-close .about__text-mobile-hidden {
  @media (max-width: 767px) {

    display: none;
  }
}
